.page {
  scroll-behavior: smooth;

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 120px;

    &-title {
      @extend %h3;
      color: $color-black;
    }
  }

  &__body {
    &--with-menu {
      overflow: hidden;
    }
  }
}
